const getData = () => {
    let res = fetchInterestsMock()
    return res
};



const postData = (url, data) => {
    let res = fetchMock()
    return res
};
function fetchMock() {
    return Promise.resolve("done")
}
export function getInterests(onSuccess) {
    getData()
        .then(res => {
        onSuccess(res.interests)
    })
}

